<template>
    <div class="Answerresult">
        <div class="Answerresult-Content">
            <div class="Answerresult-header">
                <span class="left flex-item">
                    <i @click="goState" class="fa fa-angle-left"></i>
                </span>
                <span class="center flex-item">本次成绩</span>
                <span class="right flex-item">
                    <i class="fa fa-share-alt"></i>
                </span>
            </div>
            <div class="content">
                <!-- {{answertrue.answertrue}} -->
                <canvas ref="canvas" id="canvas" width="200" height="200"></canvas>
                <div class="appraise">
                    <div class="appraise-left">
                        <div class="car-icon">
                            <i class="fa fa-car"></i>
                        </div>
                        <div class="appraise-ch">
                            <p class="get">获得称号</p>
                            <p class="killer">{{pinfen}}</p>
                        </div>
                    </div>
                    <div class="appraise-right">
                        <button class="btn-alert">快速提分</button>
                    </div>
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="card">
                <ul v-for="(item, index) in cardMessage" :key="index">
                    <li>
                        <div class="card-content">
                            <p class="title">{{item.title}}<span v-if="index===0" class="hot">热门</span></p>
                            <p class="text">{{item.text}}</p>
                        </div>
                        <div class="card-right">
                            <i class="fa fa-angle-right"></i>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>

</template>

<script>
    let routerMete;
    let pingjia;
    var st1;

    export default {
        name: 'answerresult',
        methods: {
            goState() {
                this.$router.push('/answer')
            }
        },
        beforeRouteEnter(to, from, next) {
             
            if (to.meta.answertrue === undefined) {
                routerMete = 0
            } else {
                routerMete = to.meta.answertrue;
            }
            if (routerMete === 100) {
                pingjia = '外星人'
            } else if (routerMete >= 95) {
                pingjia = '神人'
            } else if (routerMete >= 90) {
                pingjia = '驾考牛人'
            } else if (routerMete >= 80) {
                pingjia = '酿猎惋瘟性'
            } else if (routerMete < 60) {
                pingjia = '马路杀手'
            } else {
                pingjia = ('你考试了吗xxx')
            }

            next()
        },
        mounted() {
            window.localStorage.HighestScore=routerMete //当页面进入刷新分数
            let x = 1;
            if (routerMete === 100) {
                x = 1.99999991
            } else {
                x = -(1 / 100) * (100 - routerMete)
            }

            let pingjia;
            if (routerMete === 100) {
                pingjia = '满分'
            } else if (routerMete > 90) {
                pingjia = '合格'
            } else {
                pingjia = '不合格'
            }
        
            var canvas = this.$refs.canvas;
            var ctx = canvas.getContext('2d');
            ctx.clearRect(0, 0, 200, 200);
            ctx.textAlign = 'center';
            ctx.beginPath()
            ctx.font = '5rem sans-serif';
            ctx.fillStyle = 'white';
            ctx.fillText(routerMete, 100, 100, 200);
            ctx.beginPath()
            ctx.font = '2rem sans-serif';
            ctx.fillStyle = 'white';
            ctx.fillText(pingjia, 100, 140, 200)
            ctx.lineWidth = 10;
            ctx.lineCap = 'round';
            ctx.arc(100, 100, 90, 0, 1 * Math.PI, true)
            ctx.strokeStyle = 'white'
            ctx.stroke()
            ctx.beginPath();
            ctx.beginPath();
            ctx.strokeStyle = '#666666'
            ctx.lineWidth = 10;
            ctx.lineCap = 'round';
            ctx.arc(100, 100, 90, 0, x * Math.PI, true)
            ctx.stroke()
            ctx.beginPath();
        },
        data() {
            return {
                answertrue: routerMete,
                pinfen: pingjia,
                cardMessage: [{
                        title: '不过赔付',
                        text: '3小时过考试，不过最高赔付100元'
                    },
                    {
                        title: '本次错题',
                        text: '趁热打铁，及时巩固本次错题'
                    },
                    {
                        title: '弱点分析',
                        text: '强化弱点，提分更快'
                    },
                    {
                        title: '重新考试',
                        text: '发挥失常，再考一次'
                    }
                ]
            }
        },
    }
</script>

<style scope lang="less">
    .Answerresult {
        .Answerresult-Content {
            padding: 2rem;
            background: -webkit-gradient(radial, 50% 50%, 40, 50% 50%, 200, from(rgba(245, 146, 16, 0.918)), to(rgba(241, 94, 9, 0.973)));

            .Answerresult-header {
                display: flex;
                width: 100%;
                color: white;

                .flex-item {
                    width: 100%;
                    line-height: 2.64rem;
                }

                .left {
                    text-align: left;
                    font-size: 2rem;
                }

                .center {
                    text-align: center;
                    font-size: 1.5rem;
                }

                .right {
                    text-align: right;
                    font-size: 2rem;
                }
            }

            .content {
                padding: 4rem 0;
                display: flex;
                justify-content: center;
                flex-wrap: wrap;



                .appraise {
                    display: flex;
                    flex-basis: 100%;
                    margin-top: 3rem;

                    .appraise-left {
                        display: flex;

                        .car-icon {
                            display: flex;
                            display: inline-block;
                            line-height: 6rem;
                            width: 5rem;
                            height: 5rem;
                            border-radius: 50%;
                            background: rgba(21, 124, 221, 0.788);
                            text-align: center;

                            i {
                                font-size: 3rem;
                                color: rgb(224, 75, 75);
                            }
                        }

                        .appraise-ch {
                            margin-left: 1rem;
                            display: inline-block;

                            .get {
                                font-size: 1.2rem;
                            }

                            .killer {
                                font-size: 2rem;
                                margin: 0.1rem 0;
                            }
                        }

                        flex-basis: 200%;
                    }

                    .appraise-right {
                        flex-basis: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        .btn-alert {
                            border-radius: 2rem;
                            text-align: center;
                            width: 100%;
                            border: none;
                            background: white;
                            height: 3rem;
                            letter-spacing: 0.3rem;
                            font-size: 1.3rem;
                            padding: 0;
                            animation: btnalert 1s infinite alternate-reverse;
                            color: rgb(221, 94, 10);

                            &:focus {
                                outline: none;
                            }
                        }
                    }
                }
            }
        }

        .footer {
            .card {
                ul {
                    li {
                        width: 100%;
                        display: flex;
                        background: white;
                        border-bottom: 0.1rem solid lightgray;
                        padding: 1.5rem;
                        box-sizing: border-box;
                    }

                    .card-content {
                        flex-basis: 100%;

                        .title {
                            font-size: 1.4rem;
                            margin-bottom: 0.5rem;

                            .hot {
                                color: white;
                                font-size: 0.8rem;
                                padding: 0.3rem 1rem;
                                border-top-left-radius: 20rem;
                                border-top-right-radius: 20rem;
                                border-bottom-left-radius: 20rem;
                                margin-left: 1rem;
                                background: rgb(255, 123, 0);
                            }
                        }

                        .text {
                            font-size: 1.2rem;
                            color: rgb(145, 127, 127);
                        }
                    }

                    .card-right {
                        display: flex;
                        align-items: center;

                        i {
                            font-size: 1.5rem;
                            color: rgb(95, 92, 92);
                        }
                    }
                }
            }
        }
    }

    @keyframes btnalert {
        0% {
            transform: rotate(-5deg)
        }

        25% {
            transform: rotate(8deg)
        }

        50% {
            transform: rotate(0deg)
        }

        60% {
            transform: rotate(0deg)
        }

        75% {
            transform: rotate(-6deg)
        }

        100% {
            transform: rotate(8deg)
        }

    }
</style>